.product_grid {
  &+.product_grid {
    margin-top: 24px;
  }
  grid-template-columns: repeat(2, minmax(0, 1fr));
  @media screen and (min-width: 768px) {
    grid-template-columns: repeat(3, minmax(0, 1fr));
  }
  @media screen and (min-width: 1024px) {
    grid-template-columns: repeat(4, minmax(0, 1fr));
  }
}

.collection_loader {
  margin-bottom: 80px;
}